.booking-container{
  width: 100%;
}
.booking{
    width: 70%;
    margin: 0 auto;
    height: 100%;
    padding: 1rem;
    display: flex;
    gap: .5rem;
    flex-direction: column;
    align-items: flex-start;
    position: relative;
  }

.booking h3{
    margin: 5px 0;
    text-transform: uppercase;
}
.res{
    height: 100%;
    width: 100%;
    background: linear-gradient(to right, crimson, aqua);  
    
    position: absolute;
    z-index: 10;
    bottom: 0;
    
    display: flex;
    flex-direction: column;
    gap: 10rem;
    align-items: center;
    justify-content: center;

    animation: slide-x 700ms ease ;
    
}
.res span{
    color: white;
    font-size: 12px;
    font-weight: 600; 
    letter-spacing: 3px;
    text-transform: uppercase;
    padding: 1rem;
    background-color: black;


    animation: slide-up 700ms ease-in;
    
}
.res .return-home{
    color: blue;
    background-color: white;
    padding: 5px 16px;
    font-size: 14px;
    text-decoration: underline;
    cursor: pointer;

}
@keyframes slide-x {
    0%{
        transform: translateX(20px);
    }
    100%{
        transform: translateX(0);
    }
}

@keyframes slide-up {
    0%{
        opacity: 0;
        transform: translateX(-20px);
    }
    50%{
        opacity: 0.3;
    }
    75%{
        opacity: 0.5;
    }
    100%{
        opacity: 1;
        transform: translateX(0);
    }
}

.booking form{
    width: 100%;
    display: flex;
    gap: 0.8rem;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
}
.booking form input{
    width: 50%;
    padding: .9rem;
    box-shadow: 0 0 4px #b6485e;
    outline: 0;
}
.booking form textarea{
    padding: 1rem;
    width: 50%;
    outline: none;
}
.booking form button{
    width: 30%;
    padding: 1rem;
    color: white;
    background-color: crimson;
    font-weight: bold;
    margin-top: 10px;
}


.select-service-dropdown{
    outline: none;
    width: 50%;
    display: flex;
    align-items: flex-start;
    padding: 0.9rem;
    box-shadow: 0 0 4px #b6485e;

}
.select-service-dropdown option{
    text-transform: capitalize;
    padding: 1px;
    border-bottom: 1px solid gray;
    outline: none;

}
.first-option-item{
    background-color: crimson;
    color: white;
}

@media screen and (max-width:1024px) {
    .booking form{
        width: 100%;
    }
    .booking form input{
        width: 70%;
    }
    .booking form textarea{
        width: 70%;
    }
}

@media screen and (max-width:900px) {
    .booking{
        display: flex;
        align-items: center;
    }
    .booking form input{
        width: 80%;
    }
    .select-service-dropdown{
        width: 80%;
    }
    .booking form textarea{
        width: 80%;
    }
    .res span{
        font-size: 10px;
    }
}


@media screen and (max-width:500px) {
    .booking{
        width: 100%;
    }
    .booking form input{
        width: 100%;
    }
    .select-service-dropdown{
        width: 100%;
    }
    .booking form textarea{
        width: 100%;
    }
    .booking form button{
        width: 50%;
    }
    .res span{
        font-size: 9px;
        text-align: center;
    }
}